import React from "react";
import { NavLink } from 'react-router-dom'

export default function Header() {
  return (
    <header>
      <nav>
        <div className="logoHolder"></div>

        <div className="menu">
          <button className="menuBtn" type="button" name="menu">
            <img
              src="./dist/images/icon-hamburger.svg"
              width="28"
              height="22"
            />
          </button>
        </div>
        <div className="socialMediaWrap">
          <ul className="socialMedia">
            <li>
              <a>
                <img
                  src="./dist/images/twitter-icon.svg"
                  width="32"
                  height="32"
                />
              </a>
            </li>
            <li>
              <a>
                <img
                  src="./dist/images/fb-icon.svg"
                  width="32"
                  height="32"
                />
              </a>
            </li>
            <li>
              <a>
                <img
                  src="./dist/images/instagram-icon.svg"
                  width="32"
                  height="32"
                />
              </a>
            </li>
          </ul>
        </div>
        <ul className="mainNav">
          <li>
            <img
              src="./dist/images/home-img.svg"
              width="32"
              height="32"
              alt=""
            />
          </li>
          <li>
            <NavLink to="/home">
              Home
            </NavLink>
          </li>
          <li>
            <NavLink to="/register">
              Registration
            </NavLink>
          </li>
          <li>
            <NavLink to="/mechanics">
              Mechanics
            </NavLink>
          </li>
          <li>
            <NavLink to="/leader">
              LEADERBOARD
            </NavLink>
          </li>

          {/**<li><a href="javascript:void(0)" title="Logout">Logout</a></li> */}

          <ul className="socialMediaMobile">
            <li>
              <a>
                <img
                  src="./dist/images/twitter-icon.svg"
                  width="32"
                  height="32"
                  alt="Starbucks Website"
                />
              </a>
            </li>
            <li>
              <a>
                <img
                  src="./dist/images/fb-icon.svg"
                  width="32"
                  height="32"
                  alt="Starbucks Facebook"
                />
              </a>
            </li>
            <li>
              <a>
                <img
                  src="./dist/images/instagram-icon.svg"
                  width="32"
                  height="32"
                  alt="Starbucks Instagram"
                />
              </a>
            </li>
          </ul>
        </ul>
        <div className="navBorderLeftBottom"></div>
        <div className="navBorderRightBottom"></div>
      </nav>
    </header>
  );
}
