import React, { useEffect } from "react";
import { NavLink } from "react-router-dom"

export default function Vote() {

    useEffect(()=>{
      window.$(document).ready(function(){

        window.$('.votePage .voteSquareBtn').addClass('buttonDisable');
    
      
        function votechoco() {
          window.$('.swipeWrap').removeClass('swipeSelectCaramel');
          window.$('.swipeWrap').removeClass('active-caramel');
          window.$('.swipeWrap').addClass('swipeSelectChoco');
          window.$('.swipeWrap').addClass('active-choco');
          window.$('.chocoHolder').css({'opacity':'1', 'transform': 'scale(1)'});
          window.$('.caramelHolder').css({'opacity':'0.5', 'transform': 'scale(0.6)'});
          window.$('.chocolate-header').addClass('animated fadeInUp').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function(){
            window.$('.chocolate-txt1, .chocolate-txt2, .chocolate-txt3, .chocolate-txt4').addClass('animated bounceIn');
            });
            window.$('.caramel-header').removeClass('animated fadeInUp');
            window.$('.caramel-txt1, .caramel-txt2, .caramel-txt3, .caramel-txt4').removeClass('animated bounceIn');
            window.$('.voteSquareBtn').data('data-id', 2);
        }
    
        function votecaramel() {
          window.$('.swipeWrap').removeClass('swipeSelectChoco');
          window.$('.swipeWrap').removeClass('active-choco');
          window.$('.swipeWrap').addClass('swipeSelectCaramel');
          window.$('.swipeWrap').addClass('active-caramel');
          window.$('.caramelHolder').css({'opacity':'1', 'transform': 'scale(1)'});
          window.$('.chocoHolder').css({'opacity':'0.5', 'transform': 'scale(0.6)'});
          window.$('.caramel-header').addClass('animated fadeInUp').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function(){
            window.$('.caramel-txt1, .caramel-txt2, .caramel-txt3, .caramel-txt4').addClass('animated bounceIn');
            });
            window.$('.chocolate-header').removeClass('animated fadeInUp');
            window.$('.chocolate-txt1, .chocolate-txt2, .chocolate-txt3, .chocolate-txt4').removeClass('animated bounceIn');
            window.$('.voteSquareBtn').data('data-id', 1);
        }
    
        function buttonenable() {
          window.$('.votePage, .voteSquareBtn').removeClass('buttonDisable');
        }
    
    
        // $('.voteArrowLeft').click(function(){
        // 	votecaramel();
        // 	buttonenable();
        // });
    
        // $('.voteArrowRight').click(function(){
        // 	votechoco();
        // 	buttonenable();
        // });
    
        window.$('.touchevents, .caramelHolder').click(function(){
          votecaramel();
          buttonenable();
        });
    
        window.$('.touchevents, .chocoHolder').click(function(){
          votechoco();
          buttonenable();
        });
    
    
        window.$(".touchevents, .votePage").on("swipeleft",function(){
          votechoco();
          buttonenable();
        });
    
        window.$(".touchevents, .votePage").on("swiperight",function(){
          votecaramel();
          buttonenable();
        });
    
        window.$('.no-touchevents, .caramelHolder').on('mouseenter', function(){
          votecaramel();
          buttonenable();
        });
    
        window.$('.no-touchevents, .chocoHolder').on('mouseenter', function(){
          votechoco();
          buttonenable();
        });
    
    });
    },[])

  return (
    <div className="mainwrap page-vote">
      <div className="votePage">
        <div className="titleHolder">
          <img src="dist/images/main-title.png" alt="" />
        </div>

        <label>Swipe to choose your beverage!</label>
        <div className="directionWrap">
          <div className="direction">
            <div className="directionImgHolder">
              <a className="voteArrowLeft" href="#">
                <img src="dist/images/vote-arrow-left.png" alt="" />
              </a>
              <a className="voteArrowRight" href="#">
                <img src="dist/images/vote-arrow-right.png" alt="" />
              </a>
            </div>
          </div>
        </div>
        <div className="swipeContainer">
          <div className="swipeWrap">
            <div className="caramelHolder">
              <div className="caramel-header">
                <img
                  src="dist/images/caramel-header.png"
                  width="273"
                  height="135"
                  alt="Team Chocolate Hazelnut"
                />
              </div>
              <div className="caramel-txt1">
                <img
                  src="dist/images/caramel-txt1.png"
                  width="191"
                  height="45"
                  alt="Delicious Hazelnut"
                />
              </div>
              <div className="caramel-txt2">
                <img
                  src="dist/images/caramel-txt2.png"
                  width="164"
                  height="72"
                  alt="Velvety Steamed Milk"
                />
              </div>
              <div className="caramel-txt3">
                <img
                  src="dist/images/caramel-txt3.png"
                  width="191"
                  height="48"
                  alt="Top Quality Espresso"
                />
              </div>
              <div className="caramel-txt4">
                <img
                  src="dist/images/caramel-txt4.png"
                  width="136"
                  height="73"
                  alt="Chocolaty Nutty Drizzle"
                />
              </div>
              <picture>
                <source
                  media="(min-width: 1000px)"
                  srcSet="dist/images/caramel-blank.png"
                />
                <img src="dist/images/caramel.png" alt="Team Roasted Caramel" />
              </picture>
            </div>
            <div className="chocoHolder">
              <div className="chocolate-header">
                <img
                  src="dist/images/chocolate-header.png"
                  width="272"
                  height="153"
                  alt="Team Chocolate Hazelnut"
                />
              </div>
              <div className="chocolate-txt1">
                <img
                  src="dist/images/chocolate-txt1.png"
                  width="190"
                  height="25"
                  alt="Delicious Hazelnut"
                />
              </div>
              <div className="chocolate-txt2">
                <img
                  src="dist/images/chocolate-txt2.png"
                  width="135"
                  height="65"
                  alt="Velvety Steamed Milk"
                />
              </div>
              <div className="chocolate-txt3">
                <img
                  src="dist/images/chocolate-txt3.png"
                  width="135"
                  height="50"
                  alt="Top Quality Espresso"
                />
              </div>
              <div className="chocolate-txt4">
                <img
                  src="dist/images/chocolate-txt4.png"
                  width="190"
                  height="25"
                  alt="Chocolaty Nutty Drizzle"
                />
              </div>
              <picture>
                <source
                  media="(min-width: 1000px)"
                  srcSet="dist/images/chocolate-blank.png"
                />
                <img
                  src="dist/images/chocolate.png"
                  alt="Team Chocolate Hazelnut"
                />
              </picture>
            </div>
          </div>
        </div>

        <div className="buttonContainer">
          <div className="buttonHolder">
            <div className="buttonWrap">
              <NavLink to="/leader" className="leaderboardBtn">Leaderboard</NavLink>
            </div>
            <div className="buttonWrap">
              <button className="voteSquareBtn" type="button" name="button">
                <NavLink to="/thank">VOTE NOW</NavLink>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
