import React, { useEffect } from "react";
import { NavLink } from "react-router-dom";

export default function Thank() {

    useEffect(()=>{
      window.$(document).ready(function(){
        
        window
          .$(".vote-chocolate, .vote-caramel")
          .addClass("animated bounceIn")
          .one(
            "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
            function () {
              window
                .$(".chocolate-header, .caramel-header")
                .addClass("animated fadeInUp");
              window.$(".caramel-txt1").addClass("animated bounceIn");
              window.$(".caramel-txt2").addClass("animated bounceIn");
              window.$(".caramel-txt3").addClass("animated bounceIn");
              window.$(".caramel-txt4").addClass("animated bounceIn");
              window.$(".chocolate-txt1").addClass("animated bounceIn");
              window.$(".chocolate-txt2").addClass("animated bounceIn");
              window.$(".chocolate-txt3").addClass("animated bounceIn");
              window.$(".chocolate-txt4").addClass("animated bounceIn");
            }
          );
 
      });
    },[])

  return (
    <div className="mainwrap page-thankyou_caramel">

    <div className="thankyouPage">
    
      <div className="titleHolder">
        <img src="dist/images/main-title.png" alt="" />
      </div>
    
        <div className="message1">
          Thank you for voting!
        </div>
    
        <div className="vote-caramel">
          <div className="caramel-header">
            <img src="dist/images/caramel-header.png" alt="Team Chocolate Hazelnut"/>
          </div>
          <div className="caramel-txt1">
            <img src="dist/images/caramel-txt1.png" alt="Delicious Hazelnut"/>
          </div>
          <div className="caramel-txt2">
            <img src="dist/images/caramel-txt2.png" alt="Velvety Steamed Milk"/>
          </div>
          <div className="caramel-txt3">
            <img src="dist/images/caramel-txt3.png" alt="Top Quality Espresso"/>
          </div>
          <div className="caramel-txt4">
            <img src="dist/images/caramel-txt4.png" alt="Chocolaty Nutty Drizzle"/>
          </div>
          <picture>
            <source media="(min-width: 1000px)" srcSet="dist/images/caramel-blank.png"/>
            <img src="dist/images/caramel.png" alt="Team Chocolate Hazelnut"/>
          </picture>
        </div> 
    
      <div className="buttonsContainer">
      <div className="buttons">
        <div className="buttonWrap">
          <button className="fbBtn" type="button" name="button"><img src="dist/images/thankyou-fb-icon.svg" width="12" height="23" alt="" />Share on Facebook</button>
        </div>
    
        <div className="buttonWrap">
          <NavLink className="leaderboardBtn" to="/leader">Leaderboard</NavLink>
        </div>
    
        <div className="buttonWrap">
          <NavLink className="doneBtn" to="/leader">DONE</NavLink>
        </div>
      </div>
      </div>
    
    
    
    </div>
    </div>
  );
}
