import React, { useEffect } from "react";

export default function Leader() {

  useEffect(()=>{
    window.$(document).ready(function(){

      function reset() {
        window.$('.winnerContainer, .msgWrap').removeClass('luzon');
        window.$('.winnerContainer, .msgWrap').removeClass('visayas');
        window.$('.winnerContainer, .msgWrap').removeClass('mindanao');
      }
    
      let winner = 1;
    
    
      // click start
      window.$('.rightArrow').click(function(){
          winner = winner + 1;
    
          if (winner === 1) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('luzon');
          }
    
    
          if (winner === 2) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('visayas');
          }
    
          if (winner === 3) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('mindanao');
          }
    
          if (winner >= 4) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('luzon');
            winner = 1;
          }
        });
    
        window.$('.leftArrow').click(function(){
          winner = winner - 1;
    
          if (winner < 1) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('mindanao');
            winner = 3;
          }
    
          if (winner === 1) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('luzon');
          }
    
    
          if (winner === 2) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('visayas');
          }
    
          if (winner === 3) {
            reset();
            window.$('.winnerContainer, .msgWrap').addClass('mindanao');
          }
        });
        // click end
    
    
    
    
      // swipe start
      window.$('.winner').on('swipeleft',function(){
        winner = winner + 1;
    
        if (winner === 1) {
          reset();
          window.$('.winnerContainer, .msgWrap').addClass('luzon');
        }
    
    
        if (winner === 2) {
          reset();
          window.$('.winnerContainer, .msgWrap').addClass('visayas');
        }
    
        if (winner === 3) {
          reset();
          window.$('.winnerContainer, .msgWrap').addClass('mindanao');
        }
    
        if (winner >= 4) {
          winner = 3;
        }
      });
    
    
      window.$('.winner').on('swiperight',function(){
        winner = winner - 1;
    
    
        if (winner < 1) {
          winner = 1;
        }
    
    
        if (winner === 1) {
          reset();
          window.$('.winnerContainer, .msgWrap').addClass('luzon');
        }
    
    
        if (winner === 2) {
          reset();
          window.$('.winnerContainer, .msgWrap').addClass('visayas');
        }
    
        if (winner === 3) {
          reset();
          window.$('.winnerContainer, .msgWrap').addClass('mindanao');
        }
      });
      // swipe end
    
    
      window.$(window).resize(function(){
        reset();
      });
    
    
    });
    
  },[])

  return (
    <div className="mainwrap page-winner">
      <div className="winnerPage">
        <div className="titleHolder">
          <img src="dist/images/main-title.png" alt="" />
        </div>

        <div className="winner">
          <div className="winnerWrap">
            <div className="winnerContainer">
              <div className="imgHolder">
                <img src="dist/images/winner-caramel-bg.png" alt="" />
              </div>
            </div>

            <div className="winnerContainer">
              <div className="imgHolder">
                <img src="dist/images/winner-chocolate-bg.png" alt="" />
              </div>
            </div>

            <div className="winnerContainer">
              <div className="imgHolder">
                <img src="dist/images/winner-caramel-bg.png" alt="" />
              </div>
            </div>
          </div>

          <div className="infoWrap">
            <div className="msgWrap">
              <div className="msgContainer">
                <div className="msgWinnerLuzon">Leading drink in Luzon</div>
              </div>
              <div className="voteCount">65%</div>
            </div>

            <div className="msgWrap">
              <div className="msgContainer">
                <div className="msgWinnerVisayas">Leading drink in Visayas</div>
              </div>
              <div className="voteCount">25%</div>
            </div>

            <div className="msgWrap">
              <div className="msgContainer">
                <div className="msgWinnerMindanao">
                  Leading drink in Mindanao
                </div>
              </div>
              <div className="voteCount">10%</div>
            </div>

            <div className="winnerSlider">
              <div className="leftArrow"> {`<`} </div>
              <div className="rightArrow"> {`>`} </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
