import React, { useEffect } from "react";
import './asset/scss/animate.css'
import './asset/scss/flipclock.css'
import './asset/scss/mainstyle.scss'
import { BrowserRouter, Switch, Route, HashRouter } from 'react-router-dom'

import Header from "./include/Header"
import Footer from "./include/Footer"
import Home from "./Home"
import Register from "./Register"
import Mechanics from "./Mechanics"
import Leader from "./Leader"
import Location from "./Location"
import Vote from "./Vote"
import Thank from "./Thank"

function App() {

  useEffect(()=>{
    window.$(document).ready(function(){
      window.$('.menuBtn').click(function(){
        window.$('.mainNav').animate({'height':'toggle'}, 300);
      });
      window.$(window).resize(function(){
        window.$('.mainNav').css({'display':''});
      });
    });
  },[])

  /**
   * using HashRoter instead of BrowserRouter to enable refresh route on shared hosting but may cause '#' in url
   * https://reactrouter.com/web/api/HashRouter
   */
  return (
    <div className="App mainwrap">
      <HashRouter basename="/">
          <Header />
          <Switch>
            <Route exact path='/home' component={Home} />
            <Route exact path='/register' component={Register} />
            <Route exact path='/mechanics' component={Mechanics} />
            <Route exact path='/leader' component={Leader} />
            <Route exact path='/location' component={Location} />
            <Route exact path='/vote' component={Vote} />
            <Route exact path='/thank' component={Thank} />
            <Route exact path='**' component={Home} />
          </Switch>
      </HashRouter>
      <Footer/>
    </div>
  );
}

export default App;
