import React from "react";

function Mechanics() {
  return (
    <div className="mainwrap page-mechanics">
      <div className="mechanicsPage">
        <div className="titleHolder">
          <img src="dist/images/main-title.png" alt="" />
        </div>

        <div className="titlewrap">
          <div className="title">Mechanics</div>
        </div>

        <div className="content">
          <div className="columnLeft">
            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">1</div>
                </div>
              </div>
              <p>
                From February 23 to March 4,2016, Starbucks Philippines will run
                an online poll to ask fans which of the two (2) featured
                Macchiato beverages is their favorite. Fans can vote on what
                team they’re on: Team Roasted Caramel or Team Chocolate
                Hazelnut. Voting will be per region (Luzon, Visayas, Mindanao).
              </p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">2</div>
                </div>
              </div>
              <p>
                Voting will be done through www.starbuckspromotions.ph and fans
                will be required to register through their e-mail address or
                through their Facebook account.
              </p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">3</div>
                </div>
              </div>
              <p>
                The winning Macchiato beverage will be announced on March 5,
                2016 via Starbucks Philippines Facebook Page and thru the Daily
                Offerings Board in Starbucks stores.
              </p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">4</div>
                </div>
              </div>
              <p>
                The winning Macchiato beverage per region (Luzon, Visayas and
                Mindanao) will be celebrated with a special Macchiato “Buy One,
                Get One” offer on March 7, 2016 in all Starbucks stores
                nationwide.
              </p>
            </div>
          </div>

          <div className="columnRight">
            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">5</div>
                </div>
              </div>
              <p>
                The Macchiato Buy One, Get One event is valid only for the
                winning Macchiato beverage (hot and iced, of any size) for each
                region.
              </p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">6</div>
                </div>
              </div>
              <p>
                There is no limit to the number of winning Macchiato beverage
                that customers may purchase during the “Buy One, Get One” promo.
              </p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">7</div>
                </div>
              </div>
              <p>Cannot be combined with other offers or promotions.</p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">8</div>
                </div>
              </div>
              <p style={{ width: "70%" }}>
                Beverage star will be given for the purchased beverage when paid
                using the Starbucks Card.
              </p>
            </div>

            <div className="paragraph">
              <div className="numWrap">
                <div className="numContainer">
                  <div className="num">9</div>
                </div>
              </div>
              <p>Promotion will run in all Starbucks stores nationwide.</p>
            </div>

            <div className="dti">
              Per DTI-FTEB SPD Permit No. _______, Series of 2016.
            </div>
          </div>
        </div>

        <div className="imgHolder">
          <img src="dist/images/mechanics-coffee.png" alt="" />
        </div>
      </div>
    </div>
  );
}

export default Mechanics;
