import React from "react";
import { NavLink } from "react-router-dom"

export default function Location() {
  return (
    <div className="mainwrap page-location">
      <div className="locationPage">
        <div className="titleHolder">
          <img src="dist/images/main-title.png" alt="Location Header" />
        </div>
        <div className="titlewrap">
          <div className="title">Your Location?</div>
        </div>
        <div className="buttons">
          <div className="buttonWrap">
            <NavLink to="/vote" className="luzonBtn">Luzon</NavLink>
          </div>
          <div className="buttonWrap">
            <NavLink to="/vote" className="visayasBtn">Visayas</NavLink>
          </div>

          <div className="buttonWrap">
            <NavLink to="/vote" className="mindanaoBtn">Visayas</NavLink>
          </div>
        </div>
      </div>
    </div>
  );
}
