import React, { useEffect } from "react";
import { NavLink } from "react-router-dom"

export default function Home() {
  useEffect(() => {
    window.$(document).ready(function () {
      /*POPUP*/
      if (window.$(".pop-cover").length) {
        window.$(".pop-cover").hide();

        window.$(document).on("click", ".pop", function () {
          let popcont = this.rel;
          window.$(".pop-cover").fadeIn();
          window.$(".pop-up").hide();
          window.$("." + popcont).show();
        });

        // close pop-up button
        window.$(".pop-up").on("click", ".close", function () {
          window.$(".pop-cover").fadeOut();
        });
      }

      /*TAB CONTROL*/
      window.$(".tabs").on("click", "li", function () {
        let target = window.$(this).data("target");
        window.$(this).addClass("active").siblings().removeClass("active");
        window
          .$("." + target)
          .show()
          .siblings()
          .hide();
      });

      /*ACCORDION TOGGLE*/
      window.$(".accordion").on("click", "li", function () {
        window
          .$(this)
          .siblings()
          .removeClass("active")
          .end()
          .addClass("active");
      });

      /*  HTMLInspector.inspect();*/
    });

    window
      .$(".mainwrap")
      .imagesLoaded()
      .always(function (instance) {
        if (window.$(".page-landing").length) {
          setTimeout(function () {
            window.$("html, body").animate(
              {
                scrollTop: 180,
              },
              800
            );
          }, 800);
        }
        window.$("body").removeClass("stop-scrolling");
        window.$("#loader-wrapper").addClass("loaded");
        window
          .$(".caramelImgHolder")
          .addClass("animated bounceInLeft")
          .one(
            "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
            function () {
              window
                .$(".caramel-header, .chocolate-header")
                .addClass("animated fadeInUp")
                .one(
                  "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
                  function () {
                    window.$(".caramel-txt1").addClass("animated bounceIn");
                    window.$(".caramel-txt2").addClass("animated bounceIn");
                    window.$(".caramel-txt3").addClass("animated bounceIn");
                    window.$(".caramel-txt4").addClass("animated bounceIn");
                    window.$(".chocolate-txt1").addClass("animated bounceIn");
                    window.$(".chocolate-txt2").addClass("animated bounceIn");
                    window.$(".chocolate-txt3").addClass("animated bounceIn");
                    window.$(".chocolate-txt4").addClass("animated bounceIn");
                    window.$(".caramel-bean1").addClass("animated bounceIn");
                    window.$(".caramel-bean2").addClass("animated bounceIn");
                    window.$(".caramel-bean3").addClass("animated bounceIn");
                    window.$(".choco-bean1").addClass("animated bounceIn");
                    window.$(".choco-bean2").addClass("animated bounceIn");
                    window.$(".choco-bean3").addClass("animated bounceIn");
                  }
                );
            }
          );

        window.$(".chocoImgHolder").addClass("animated bounceInRight");
        window
          .$(".graph-luzon")
          .addClass("animated fadeInDown")
          .one(
            "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
            function () {
              window
                .$(".graph-visayas")
                .addClass("animated fadeInDown")
                .one(
                  "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
                  function () {
                    window
                      .$(".graph-mindanao")
                      .addClass("animated fadeInDown")
                      .one(
                        "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
                        function () {
                          window
                            .$(".landingPage .buttonWrap")
                            .addClass("animated fadeInUp");
                          window
                            .$(".countdownWrap")
                            .addClass("animated fadeInDown")
                            .one(
                              "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
                              function () {
                                window
                                  .$(
                                    ".luzonCaramelVotes, .visayasCaramelVotes, .mindanaoCaramelVotes, .luzonChocoVotes, .visayasChocoVotes, .mindanaoChocoVotes, .luzonCaramelPercentBar, .visayasCaramelPercentBar, .mindanaoCaramelPercentBar"
                                  )
                                  .addClass("removedelay");
                                window
                                  .$(".caramel-header, .chocolate-header")
                                  .removeClass("fadeInUp")
                                  .addClass("pause");
                              }
                            );
                        }
                      );
                  }
                );
            }
          );

        window.$(".whichTeam").addClass("animated fadeIn");
        window.$(".vs").addClass("animated bounceIn");

        window.$(".titleanimate").addClass("animated fadeInDown");

        window
          .$(".vote-chocolate, .vote-caramel")
          .addClass("animated bounceIn")
          .one(
            "webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend",
            function () {
              window
                .$(".chocolate-header, .caramel-header")
                .addClass("animated fadeInUp");
              window.$(".caramel-txt1").addClass("animated bounceIn");
              window.$(".caramel-txt2").addClass("animated bounceIn");
              window.$(".caramel-txt3").addClass("animated bounceIn");
              window.$(".caramel-txt4").addClass("animated bounceIn");
              window.$(".chocolate-txt1").addClass("animated bounceIn");
              window.$(".chocolate-txt2").addClass("animated bounceIn");
              window.$(".chocolate-txt3").addClass("animated bounceIn");
              window.$(".chocolate-txt4").addClass("animated bounceIn");
            }
          );
      })
      .progress(function (instance, image) {});

      const today = new Date();
	  const endDate = new Date("2020-12-25T00:00:00");
	  const dif = today.getTime() - endDate.getTime();

	  const seconds = Math.abs(dif / 1000);

	  const clock = window.$('.clock').FlipClock(seconds, {
	    clockFace: 'DailyCounter',
	    countdown: true
      });
      

      const luzonCaramelPercent = window.$('.luzonCaramelPercent').text().replace(/[a-z]\s/, '');
      const luzonChocoPercent = window.$('.luzonChocoPercent').text().replace(/[a-z]\s/, '');
  
      const visayasCaramelPercent = window.$('.visayasCaramelPercent').text().replace(/[a-z]\s/, '');
      const visayasChocoPercent = window.$('.visayasChocoPercent').text().replace(/[a-z]\s/, '');
  
      const mindanaoCaramelPercent = window.$('.mindanaoCaramelPercent').text().replace(/[a-z]\s/, '');
      const mindanaoChocoPercent = window.$('.mindanaoChocoPercent').text().replace(/[a-z]\s/, '');
  
      window.$('.luzonCaramelVotes').css({'width': luzonCaramelPercent});
      window.$('.luzonChocoVotes').css({'width': luzonChocoPercent});
  
      window.$('.visayasCaramelVotes').css({'width': visayasCaramelPercent});
      window.$('.visayasChocoVotes').css({'width': visayasChocoPercent});
  
      window.$('.mindanaoCaramelVotes').css({'width': mindanaoCaramelPercent});
      window.$('.mindanaoChocoVotes').css({'width': mindanaoChocoPercent});

  }, []);

  return (
    <div className="mainwrap page-landing">
      <div className="landingPage">
        <div className="countdownWrap">
          <label className="labeltime">Time left to vote</label>
          <label className="clock"></label>
        </div>
        <div className="titleHolder titleanimate">
          <img src="./dist/images/main-title.png" alt="" />
        </div>
        <div className="whichTeam">
          Which <span className="team">TEAM</span> are you on?
        </div>

        <div className="coffeeWrap">
          <div className="caramelImgHolder">
            <div className="caramel-header">
              <img
                src="./dist/images/caramel-header.png"
                alt="Team Roasted Caramel"
              />
            </div>
            <div className="caramel-txt1">
              <img
                src="./dist/images/caramel-txt1.png"
                alt="Roasted Caramel Drizzle"
              />
            </div>
            <div className="caramel-txt2">
              <img
                src="./dist/images/caramel-txt2.png"
                alt="Top Quality Espresso"
              />
            </div>
            <div className="caramel-txt3">
              <img
                src="./dist/images/caramel-txt3.png"
                alt="Rich Caramel Taste"
              />
            </div>
            <div className="caramel-txt4">
              <img
                src="./dist/images/caramel-txt4.png"
                alt="Velvety Steamed Milk"
              />
            </div>
            <picture>
              <source
                media="(min-width: 1000px)"
                srcSet="./dist/images/caramel-blank.png"
              />
              <img src="./dist/images/caramel.png" alt="Team Roasted Caramel" />
            </picture>

            <div className="caramel-bean1">
              <img src="./dist/images/caramel-bean1.png" alt="" />
            </div>

            <div className="caramel-bean2">
              <img src="./dist/images/caramel-bean2.png" alt="" />
            </div>

            <div className="caramel-bean3">
              <img src="./dist/images/caramel-bean2.png" alt="" />
            </div>
          </div>

          <div className="vs">
            <img src="./dist/images/vs.png" alt="" />
          </div>

          <div className="chocoImgHolder">
            <div className="chocolate-header">
              <img
                src="./dist/images/chocolate-header.png"
                alt="Team Chocolate Hazelnut"
              />
            </div>
            <div className="chocolate-txt1">
              <img
                src="./dist/images/chocolate-txt1.png"
                alt="Delicious Hazelnut"
              />
            </div>
            <div className="chocolate-txt2">
              <img
                src="./dist/images/chocolate-txt2.png"
                alt="Velvety Steamed Milk"
              />
            </div>
            <div className="chocolate-txt3">
              <img
                src="./dist/images/chocolate-txt3.png"
                alt="Top Quality Espresso"
              />
            </div>
            <div className="chocolate-txt4">
              <img
                src="./dist/images/chocolate-txt4.png"
                alt="Chocolaty Nutty Drizzle"
              />
            </div>
            <picture>
              <source
                media="(min-width: 1000px)"
                srcSet="./dist/images/chocolate-blank.png"
              />
              <img
                src="./dist/images/chocolate.png"
                alt="Team Chocolate Hazelnut"
              />
            </picture>

            <div className="choco-bean1">
              <img src="./dist/images/choco-bean1.png" alt="" />
            </div>

            <div className="choco-bean2">
              <img src="./dist/images/choco-bean2.png" alt="" />
            </div>

            <div className="choco-bean3">
              <img src="./dist/images/choco-bean3.png" alt="" />
            </div>
          </div>
        </div>

        <div className="buttonWrap">
          <div className="wrap">
            <NavLink className="voteNowBtn" to="/register">VOTE NOW</NavLink>
          </div>
        </div>

        <div className="barGraphWrap">
          <div className="voteTallyMobile">
            <div className="graph-luzon">
              <div className="locationName">Luzon</div>
              <div className="voteTallyWrap">
                <div className="voteTallyContainer">
                  <div className="luzonCaramelPercentBar"></div>
                  <div className="luzonChocoPercentBar"></div>
                  <div className="luzonCaramelPercentLabel">30%</div>
                  <div className="luzonChocoPercentLabel">70%</div>
                </div>
              </div>
            </div>

            <div className="graph-visayas">
              <div className="locationName">Visayas</div>
              <div className="voteTallyWrap">
                <div className="voteTallyContainer">
                  <div className="visayasCaramelPercentBar"></div>
                  <div className="visayasChocoPercentBar"></div>
                  <div className="visayasCaramelPercentLabel">10%</div>
                  <div className="visayasChocoPercentLabel">90%</div>
                </div>
              </div>
            </div>

            <div className="graph-mindanao">
              <div className="locationName">Mindanao</div>
              <div className="voteTallyWrap">
                <div className="voteTallyContainer">
                  <div className="mindanaoCaramelPercentBar"></div>
                  <div className="mindanaoChocoPercentBar"></div>
                  <div className="mindanaoCaramelPercentLabel">80%</div>
                  <div className="mindanaoChocoPercentLabel">20%</div>
                </div>
              </div>
            </div>
          </div>

          <div className="voteTallyDesktop">
            <div className="votesContainer graph-luzon">
              <div className="votesCaramelWrap">
                <div className="luzonCaramelVotes"></div>
                <div className="luzonCaramelPercent">80%</div>
              </div>
              <div className="locationName">Luzon</div>
              <div className="votesChocoWrap">
                <div className="luzonChocoVotes"></div>
                <div className="luzonChocoPercent">20%</div>
              </div>
            </div>

            <div className="votesContainer graph-visayas">
              <div className="votesCaramelWrap">
                <div className="visayasCaramelVotes"></div>
                <div className="visayasCaramelPercent">60%</div>
              </div>
              <div className="locationName">Visayas</div>
              <div className="votesChocoWrap">
                <div className="visayasChocoVotes"></div>
                <div className="visayasChocoPercent">40%</div>
              </div>
            </div>

            <div className="votesContainer graph-mindanao">
              <div className="votesCaramelWrap">
                <div className="mindanaoCaramelVotes"></div>
                <div className="mindanaoCaramelPercent">10%</div>
              </div>
              <div className="locationName">Mindanao</div>
              <div className="votesChocoWrap">
                <div className="mindanaoChocoVotes"></div>
                <div className="mindanaoChocoPercent">90%</div>
              </div>
            </div>
          </div>
        </div>

        <div className="onecopy animated fadeIn">One for you, One to share. </div>
        <p className="twocopy">The winning Macchiato beverage per region (Luzon, Visayas, Mindanao) will be celebrated with a "Buy one, Get one" offer on March 7, 2016 in all Starbucks stores in the Philippines.</p>

      </div>
    </div>
  );
}
