import React from 'react'
import { NavLink } from "react-router-dom"

export default function Register() {
    return (
        <div className="mainwrap page-register">
            <div className="registerPage">
                <div className="titleHolder"><img src="dist/images/main-title.png" alt="Registration Header" /></div>
                <div className="titlewrap">
                    <div className="title">Registration</div>
                </div>
                <div className="reg-holder">
                    <form action="">
                        <input type="text" placeholder="Full Name"/>
                        <input type="email" placeholder="Email Address"/>
                        <div className="wrap">
                            <div className="buttonWrap">
                            <button className="voteSquareBtn">
                                <NavLink to="/location">Register</NavLink>
                            </button>
                            </div>
                        </div>
                    </form>
                    <div className="linestyle">or</div>

                    <div className="wrap">
                        <label>Use your Facebook to register</label>
                        <div className="buttonWrap">
                        <button className="fbBtn" type="button" name="button"><img src="dist/images/thankyou-fb-icon.svg" width="12" height="23" alt="" />Connect with Facebook</button>
                        </div>
                        <div className="notify">
                            <p>This Facebook account has already voted.</p>
                            <p>You can only vote once.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
